import srcIcon from "@/assets/gantanghao.png";
export default {
  data() {
    return {
      topTitle: "设置",
      srcIcon: srcIcon,
      dataList: {
        phone: "",
        email: ""
      }
    };
  },
  mounted() {
    this.getSelf();
  },
  methods: {
    getSelf() {
      var that = this,
        formData = {
          sign: that.$store.state.user.sign,
          access_token: that.$store.state.user.token
        };
      that
        .$request({
          method: "POST",
          url: that.$baseURL + "/personal/user-detail",
          data: that.$qs.stringify(formData)
        })
        .then(function(response) {
          if (response.data.phone != "") {
            that.dataList.phone = response.data.phone.replace(
              /(\d{3})(\d{4})(\d{4})/,
              "$1****$3"
            );
          }
          if (response.data.email != "") {
            if (String(response.data.email).indexOf("@") > 0) {
              let str = response.data.email.split("@"),
                _s = "";
              if (str[0].length > 4) {
                _s = str[0].substr(0, 3);
                for (let i = 0; i < str[0].length - 4; i++) {
                  _s += "*";
                }
              } else {
                _s = str[0].substr(0, 1);
                for (let i = 0; i < str[0].length - 1; i++) {
                  _s += "*";
                }
              }
              that.dataList.email = _s + "@" + str[1];
              return that.dataList.email;
            }
          }
        })
        .catch(function(error) {
          console.log(error);
        });
    },
    logout() {
      var that = this;
      that.$store.dispatch("UserLogout").then(() => {
        that.$router.push({ path: "/" });
      });
      //     formData = {
      //       sign: that.$store.state.user.sign,
      //       access_token: that.$store.state.user.token
      //     };
      //   that
      //     .$request({
      //       method: "POST",
      //       // url: that.$baseURL + "/goods/goods-list",
      //       data: that.$qs.stringify(formData)
      //     })
      //     .then(function(response) {})
      //     .catch(function(error) {
      //       console.log(error);
      //     });
    }
  }
};



